.signin {
  height: 100vh;
  background: azure;

  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 25rem;
    height: 100%;
    margin: auto;

    h1 {
      color: #39f;
      border: 2px solid #39f;
      padding: 1px 8px;
      background: #fff;
      font-size: 2rem;
    }

    input,
    button {
      padding: 1px 10px;
      font-weight: 500;
      width: 20rem;
      height: 2rem;
      margin: 0.8rem;
      text-decoration: none;
      outline: none;
      font-size: 0.895rem;

      &:required {
        box-shadow: none;
      }
    }

    button {
      border-radius: 45px;
      border: none;
      cursor: pointer;
    }

    .submit {
      background: #39f;
      color: #fff;
    }

    .link {
      margin-top: 0.5rem;
      text-decoration: none;
      padding: 0.5rem 3rem;
      background: #fff;
      border: solid 1px #39f;
      color: #39f;
    }
  }
}
